@import '../../assets/sass/Common.scss';

.PagePal {
    &.l-container {
      padding: 2rem;
    }
  
    &-extractURL {
      margin-bottom: 2rem;
    }
  
    &-extractDataInput {
      width: 100%;
      padding: 0.5rem;
      margin-bottom: 0.5rem;
    }
  
    &-bottomSection {
      display: flex;
      margin-top: 2rem;
    }
  
    &-extractedData {
      flex: 1 1 50%;
      padding-right: 8px;
    }
  
    &-chatInterface {
      flex: 1 1 50%;
      display: flex;
      flex-direction: column;
      align-items: stretch;
      background-color: $brand-tertiary;
    }
  
    h2{
        text-align: center;
    }

    &-chatHistory {
      flex: 1 1 auto;
    }
  
    &-chatHistoryList {
      list-style-type: none;
      padding: 8px;
      max-height: 400px;
      overflow-y: scroll;

        li {
            margin-bottom: 8px;
            background-color: white;
            border-radius: 4px;
            padding: 4px;
        }
    }
  
    &-messageInput {
      padding: 0.5rem;
    }
  
    /* Optional styling for buttons */
    button {
      padding: 0.5rem 1rem;
      background-color: #007bff;
      color: #fff;
      border: none;
      cursor: pointer;
  
      &:hover {
        background-color: #0056b3;
      }
    }
  
    @media screen and (max-width: 768px) {
      &-extractURL {
        margin-bottom: 1rem;
      }
  
      &-bottomSection {
        flex-direction: column;
      }
  
      &-extractedData,
      &-chatInterface {
        flex: none;
        margin-right: 0;
      }
    }
  }
  